// import React from "react";
// import "./css/reset.css"
import "./css/item.css"
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import select from "./Select";

const ColdItem = props => {
  // 요청받은 정보를 담아줄 변수 선언
  const [testStr, setTestStr] = useState('');

  // 변수 초기화
  function callback(str) {
    setTestStr(str);
  }

  // 첫 번째 렌더링을 마친 후 실행
  useEffect(
    () => {
      axios({
        url: '/Refrigerator',
        method: 'GET'
      }).then((res) => {
        callback(res.data);
      })
    }, []
  );

  function test() {
    alert("DB에 저장 후 값 읽어오고 재렌더링")
  }
  function test2() {
    alert("수정")
  }

  //입력받을 값
  const { img, expiration, name, num, day } = props;

  return (
    <div>
      {props.cold && props.cold.map((item, i) => (

        <div className="item" key={i}>
          <div className="img"></div>
          <div className="info">
            <div className="first">
              <div className="date"><input className="expiration" type={"date"} /> </div>
              <div className="del" onClick={test2}>➖</div>
              <div className="chek" onClick={test}>✔️</div>
            </div>
            <div className="second">
              <div className="proName">제품명 <input className="nameInput" type={Text} /></div>
              <div className="num">
                수량 <div className="numOpsion">
                  <input className="numInput" type={Text} />
                  <select>
                    <option value={"sum"}>개</option>
                    <option value={"kg"}>kg</option>
                    <option value={"ml"}>ml</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="dead">남은 유통기한 : {day}</div>
          </div>
        </div>
      ))}

    </div>

  );
}

export default ColdItem;