import "./css/dailyMeals.css";
import Header from "./Header";
import Footer from "./Footer";
import React, { useState, useEffect, useRef } from "react";
import axios from "axios";

function DailyMeals() {
    return(
        <div className="DailyMealsForm">
            <Header />
            <DailyMealsContent />
            <Footer />
        </div>
    )
}

const viewDietClick = (d_no, diet_name, e) => {
    console.log(d_no + ", " + diet_name);

    if (d_no != null && diet_name != null) {
        window.location.href = "/detail_diet?d_no=" + d_no + "&diet_name=" + diet_name;
    } else {
        window.location.href = "/";
    }
}

function MainDietSearchResultBox(props) {
    return (
        <div className="MainRecipeRankBox" onClick={(e) => {viewDietClick(props.d_no, props.diet_name)}}>
            <div className="MainRecipeImg"><img src={props.img} alt="Blob URL Image" width={320} height={240}></img></div>
            <div className="MainRecipeName">
                <span>No. {props.d_no}</span>
                <span>{props.diet_name}</span>
            </div>
        </div>
    )
}
function DailyMealsContent() {
    const [isEmptyItem, setIsEmptyItem] = useState(false);
    const boxStyle = {
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    };

    /** useEffect로 페이지가 랜더링 될때마다 키워드를 랜덤으로 가져와서 세팅함 */
    let [keyword, setKeyword] = useState([{
        keyword1: '아이',
        keyword2: '놀이동산',
        keyword3: '자연',
        keyword4: '편의점',
        keyword5: '쉬운',
        keyword6: '늦은 밤',
        keyword7: '아침메뉴',
        keyword8: '둘이서',
    }]);

    const [dietResults, setDietResults] = useState([{
        d_no : '',
        diet_name : '',
        img : ''
    }])

    const [search, setSearch] = useState('');

    const [html, sethtml] = useState('');

    const onChangeSearch = (e) => {
        let inputValue = e.target.value;

        e.preventDefault();
        setSearch(inputValue);
        // setSearch(inputValue);
        console.log("Search: " + inputValue);
        console.log("setSearch: " + search);
    }

    const pressEnter = (e) => {
        let inputKeyword = document.getElementById('inputKeyword').value;

        if (e.keyCode == 13) {
            onChangeSearch(e);
            //엔터키가 눌릴 때
            if (e.keyCode == 13) {
                /** 
                 * 전체 검색 태그 0 -> x
                 * 레시피 검색 태그 1 
                 * 식단 검색 태그 2
                 * 각각의 태그 번호와 inputKeyword에 있는 값을 가지고 검색 구현
                 */

                if (inputKeyword.length == 0) {
                    // alert(", 검색어가 입력되지 않았습니다.");
                } else {
                    // alert(", 검색어: " + inputKeyword);
                    axios
                        .post("/search2", {
                            diet_name: inputKeyword
                        })
                        .then((res) => {

                            const _inputData = res.data.map((rowData) => (
                                {
                                    d_no: rowData.d_no,
                                    diet_name: rowData.diet_name,
                                    img: rowData.imgd,
                                })
                            )
                            console.log("_inputData : " + JSON.stringify(_inputData))

                            // if(_inputData.length==0){sethtml("검색결과가 없습니다.")}
                            // else{sethtml("")}
                            setIsEmptyItem(false);
                            if(_inputData.length > 0) {
                                setIsEmptyItem(true);
                            }
                            setDietResults(dietResults.concat(_inputData))

                        })
                    console.log(dietResults)
                    dietResults.splice(1)

                }

            }
            console.log("input : ", inputKeyword);
        }
    }

    const tag = (e) => {
                    axios
                        .post("/search2", {
                            diet_name: e
                        })
                        .then((res) => {

                            const _inputData = res.data.map((rowData) => (
                                {
                                    d_no: rowData.d_no,
                                    diet_name: rowData.diet_name,
                                    img: rowData.imgd,
                                })
                            )
                            console.log("_inputData : " + JSON.stringify(_inputData))

                            // if(_inputData.length==0){sethtml("검색결과가 없습니다.")}
                            // else{sethtml("")}
                            setIsEmptyItem(false);
                            if(_inputData.length > 0) {
                                setIsEmptyItem(true);
                            }
                            setDietResults(dietResults.concat(_inputData))

                        })
                    console.log(dietResults)
                    dietResults.splice(1)

                }

    return(
        <div className="DailyMealsContent">
            <div className="DailyMealsSearchForm">
                <span id="searchTitle">식단 찾아보기</span>
                <span id="searchSubTitle">키워드를 이용해 검색하거나 식단 이름을 직접 입력해 검색 해보세요</span>
                <div className="DailyMealsSearchBox">
                    <div className="DailyMealsInputKeyword">
                        <i className="fas fa-search" id="searchIcon"></i>
                        <input type="text" id="inputKeyword" placeholder="키워드를 입력해주세요" onKeyDown={(e) => {pressEnter(e)}}></input>
                    </div>
                </div>
                <div className="DailyMealsKeywordForm">
                    <div className="DailyMealsKeywordList" id="all" value="all"><span>전체</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword1} onClick={(e) => tag(keyword[0].keyword1)}><span>{keyword[0].keyword1}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword2} onClick={(e) => tag(keyword[0].keyword2)}><span>{keyword[0].keyword2}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword3} onClick={(e) => tag(keyword[0].keyword3)}><span>{keyword[0].keyword3}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword4} onClick={(e) => tag(keyword[0].keyword4)}><span>{keyword[0].keyword4}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword5} onClick={(e) => tag(keyword[0].keyword5)}><span>{keyword[0].keyword5}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword6} onClick={(e) => tag(keyword[0].keyword6)}><span>{keyword[0].keyword6}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword7} onClick={(e) => tag(keyword[0].keyword7)}><span>{keyword[0].keyword7}</span></div>
                    <div className="DailyMealsKeywordList" id="value" value={keyword[0].keyword8} onClick={(e) => tag(keyword[0].keyword8)}><span>{keyword[0].keyword8}</span></div>
                </div>
                {
                    isEmptyItem === false ? <div className="DailyMealsSearchResultForm" style={boxStyle}><span>검색 결과가 없습니다</span></div> :
                    <div className="DailyMealsSearchResultForm">
                        {dietResults.map((vv,index) => {
                            if(index > 0) {
                                return <MainDietSearchResultBox d_no={vv.d_no} diet_name={vv.diet_name} img={vv.img}></MainDietSearchResultBox>
                            }
                        })}
                    </div>
                }
            </div>
        </div>
    )
}


const pressEnter = (e) => {
    let inputKeyword = document.getElementById('inputKeyword').value;

    //엔터키가 눌릴 때
    if(e.keyCode == 13) {
        if(inputKeyword.length == 0) {
            alert("검색어가 입력되지 않았습니다.");
        } else {
            alert("검색어: " + inputKeyword);
        }
    }
}

export default DailyMeals;