
import "./css/reset.css"
import "./css/popup.css"
import { useEffect, useState } from 'react';
import axios from "axios";
import create from "./DietCreate"

function Popup(props){
    const popupX = (window.innerWidth / 2) - (1280 / 2);
    const popupY = (window.innerHeight / 2) - (720 / 2);

    useEffect(() => {
        document.body.style.cssText = `
          position: fixed; 
          top: -${window.scrollY}px;
          overflow-y: scroll;
          width: 100%;`;
        return () => {
          const scrollY = document.body.style.top;
          document.body.style.cssText = '';
          window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
        };
      }, []);

    const{onClose} = props;
    const{txt} = props;

    return(
        <div className="popup" onClick={()=> {
            // onClose(false);
        }}>
            <Search />
        </div>
    );
        

    
    function viewRecipeClick (recipe_nm_ko, sumry, img, e) {
        // console.log(recipe_nm_ko + ", " + sumry + ", " + img);
        // window.location.href = "/find_recipe?recipe_nm_ko="+ recipe_nm_ko + "&sumry=" + sumry + "&img=" + img;
        // const _data = {recipe_nm_ko, sumry, img}
        // const pageTitle = "diet_create"
        // window.history.pushState({recipe_nm_ko, sumry, img}, null, `/find_recipe1?${recipe_nm_ko}`)
        if(txt=="아침메뉴를 골라보세요!"){
            window.location.href = "/find_recipe1?recipe_nm_ko="+ recipe_nm_ko;
            console.log(recipe_nm_ko)
        }
        else if(txt == "점심메뉴를 골라보세요!"){
            window.location.href = "/find_recipe2?recipe_nm_ko="+ recipe_nm_ko;
            console.log(recipe_nm_ko)

        }
        else if(txt == "저녁메뉴를 골라보세요!"){
            window.location.href = "/find_recipe3?recipe_nm_ko="+ recipe_nm_ko;
            console.log(recipe_nm_ko)

        }
        
        // console.log(_data)
    }
    function MainRecipeSearchResultBox(props) {
        return (
            <div className="MainRecipeRankBox" onClick={(e) => {viewRecipeClick(props.recipe_nm_ko, props.sumry, props.img)}}>
                <div className="MainRecipeImg"><img src={props.img} alt="Blob URL Image" width={300} height={240}></img></div>
                <div className="MainRecipeName">
                    <span>No. {props.recipe_id}</span>
                    <span>{props.recipe_nm_ko}</span>
                    <span className = "none">{props.sumry}</span>
                </div>
            </div>
        )
    }

    
    function Search(){
    
        const [recipeResults, setRecipeResults] = useState([{
            recipe_id : '',
            recipe_nm_ko : '',
            img : '',
            sumry : '',
        }])
    
        const [search, setSearch] = useState('');
    
        const onChangeSearch = (e) => {
            let inputValue = e.target.value;
    
            e.preventDefault();
            setSearch(inputValue);
            // setSearch(inputValue);
            console.log("Search: " + inputValue);
            console.log("setSearch: " + search);
        }
    
        const pressEnter = (e) => {
            let inputKeyword = document.getElementById('inputKeyword').value;
    
            if (e.keyCode == 13) {
                onChangeSearch(e);
                //엔터키가 눌릴 때
                if (e.keyCode == 13) {
                    /** 
                     * 전체 검색 태그 0 -> x
                     * 레시피 검색 태그 1 
                     * 식단 검색 태그 2
                     * 각각의 태그 번호와 inputKeyword에 있는 값을 가지고 검색 구현
                     */
    
                    if (inputKeyword.length == 0) {
                        alert(", 검색어가 입력되지 않았습니다.");
                    } else {
                        // alert(", 검색어: " + inputKeyword);
                        axios
                            .post("/search1", {
                                recipe_nm_ko: inputKeyword
                            })
                            .then((res) => {
    
                                const _inputData = res.data.map((rowData) => (
                                    {
                                        recipe_id: rowData.recipe_id,
                                        recipe_nm_ko: rowData.recipe_nm_ko,
                                        sumry: rowData.sumry,
                                        img: rowData.img,
                                    })
                                )
                                console.log("_inputData : " + JSON.stringify(_inputData))
                                console.log("_inputData : " + _inputData.length)
    
                                // if(_inputData.length==0){sethtml("검색결과가 없습니다.")}
                                // else{sethtml("")}
                                setRecipeResults(recipeResults.concat(_inputData))
    
                            })
                        console.log(recipeResults)
                        recipeResults.splice(1)
    
                    }
    
                }
                console.log("input : ", inputKeyword);
                
            }
        }
    
        return(
            <div className="Search">
                <div className="SearchForm">
                    <i className="fas fa-search" id="searchIcon"></i>
                    <input type="text" id="inputKeyword" placeholder={txt}  onKeyDown={(e) => {pressEnter(e)}}></input>
                </div>
                <div className="SearchResult">
                    {recipeResults.map((v,index) => {if(index > 0){ return <MainRecipeSearchResultBox recipe_id={v.recipe_id} recipe_nm_ko={v.recipe_nm_ko} sumry={v.sumry} img={v.img}></MainRecipeSearchResultBox>}})}
                </div>
            </div>
        )
    }
}



export default Popup