import "./css/findPw.css"
import React, { useState, useEffect } from 'react';
import axios from 'axios'

function FindPw() {
	const [email, setInputEmail] = useState("");
	// const [phone, setInputPhone] = useState("");

	const handleInputEmail = (e) => {
		setInputEmail(e.target.value);
	};

	// const handleInputPhone = (e) => {
	// 	setInputPhone(e.target.value);
	// };

	return (
		<div className="FindPwForm">
			<div className="navLogo" onClick={(e) => {clickBtn("/", e)}}>
        		<span className="navLogo">CAPSTONE </span>
        		<span className="navLogo" id="LogoColor">DESIGN</span>
      		</div>
			<div className="FindPwTitle"><span>비밀번호 찾기</span></div>
			<div className="InputId">
				{/* 여기서 입력된 이메일 주소는 FindPwLast페이지로 값 전달해야함 */}
	  			<input type="text" path="email" placeholder="이메일" id="email" name="email" value={email} onChange={handleInputEmail} />
	  		</div>
			<div className="FindPwSubTitle"><span>가입하실때 사용한 이메일 주소를 입력해주세요.</span></div>
			<div className="FindPwNextBtn" onClick={(e) => {clickBtn("FindPw", e)}}>비밀번호 찾기</div>
			<div className="FindPwNotice">
				<span>가입하신 이메일 주소를 입력해주시면 비밀번호 재설정 주소가 발송됩니다.</span>
				<span>비밀번호 재설정 주소로 접속하신 뒤 새로운 비밀번호를 입력해주세요.</span>
				<span>메일이 도착하지 않았다면 스팸 메일함을 확인해주시기 바랍니다.</span>
			</div>
		</div>
	);
}

const clickBtn = (params, e) => {
    console.log(params);
    if(params == "/") {
        document.location.href = "/";
    } else if(params == "FindPw") {
        document.location.href = "/findPwLast";
    }
};

export default FindPw;
