function RecipeSelectBox(props) {
  const {} = props;
  const [img, setImg] = useState("");
  const [popup, handlePopup] = useState(false);
  // const [popup2, handlePopup2] = useState(false);
  // const [popup3, handlePopup3] = useState(false);

  return (
    <div className="RecipeSelectBox">
      <div className="Recipe">
        <span id="time">{props.time}</span>
        <div className="RecipeName">
          <span>[레시피 이름]</span>
          <span id="recipeName">{props.recipeName}</span>
        </div>
        <div className="RecipeComment">
          <span>[레시피 설명]</span>
          <span id="recipeComment">{props.recipeComment}</span>
        </div>  
      </div>
      <div className="RecipeImgBox" onClick={()=> handlePopup(true)}><img src={props.img} alt="음식 선택" width={320} height={320}></img></div>
      {popup && <Popup onClose={handlePopup} txt={"아침메뉴를 골라보세요!"}/>}

      {/* {popup2 && <Popup2 onClose={handlePopup}/>}
      {popup3 && <Popup3 onClose={handlePopup}/>} */}
    </div>
  );
}

