function HashTagForm(props) {
  const onClickDeleteHandler = (param, e) => {
    console.log(props.hashTag);
    console.log(props.setHash);
    console.log(param);

    let countArr = [...props.hashTag];
    let idx = countArr.indexOf(param);

    while(idx > -1) {
      countArr.splice(idx, 1);
      idx = countArr.indexOf(param);
    }

    console.log(countArr);

    props.setHash(countArr);
  }
  console.log(props.hash);

  return (
    <div className="hashTag">
      <div className="hashBtnForm">
        <div className="delBtn" onClick={(e)=> {onClickDeleteHandler(props.hash, e)}}>
          <i className="fas fa-times"></i>
        </div>
      </div>
      <div className="hashTagForm">
        <label>{props.hash}</label>
      </div>
    </div>
  )
}

