import Footer from "./Footer";
import Header from "./Header";
import './css/reset.css';
import './css/myPage.css';
import { useEffect, useState } from "react";
import Swal from "sweetalert2";
import axios from "axios";

function MyPage() {
  return (
    <div className="MyPageContent">
      <Header />
      <MyPageContent />
      {/* <Footer/> */}
    </div>
  )
}
function MyPageContent() {
  let uid = sessionStorage.getItem("id");
  let uName = sessionStorage.getItem("nickname");

  console.log(uid);
  console.log(uName);

  const [isEmptyItem, setIsEmptyItem] = useState(false);
  const boxStyle = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
};
  const [inputData, setInputData] = useState([{
    d_no: '',
    email: '',
    recipe_id1: '',
    recipe_id2: '',
    recipe_id3: '',
    diet_name: '',
    keyword: '',
    imgd: ''


  }])
  const run = () => {
    axios
      .post("/mypage", {
        email: uid
      })
      .then((res) => {

        const _inputData = res.data.map((rowData) => (
          {
            d_no: rowData.d_no,
            email: rowData.email,
            recipe_id1: rowData.recipe_id1,
            recipe_id2: rowData.recipe_id2,
            recipe_id3: rowData.recipe_id3,
            diet_name: rowData.diet_name,
            keyword: rowData.keyword,
            imgd: rowData.imgd,
          })
        )

        setIsEmptyItem(false);
        if(_inputData.length > 0) {
          setIsEmptyItem(true);
        }
        setInputData(inputData.concat(_inputData))
      })
  }
  useEffect(run, [])
  console.log(inputData)

  return (
    <div className="MyPageForm">
      <label>마이페이지</label>
      <div className="UserSettingForm">
        <label>계정 설정</label>
        <div className="UserInfoForm">
          <div id="user">
            <label>아이디</label>
            <label style={{ color: "rgb(50, 220, 199)" }}>{uid}</label>
          </div>
          <div id="user">
            <label>비밀번호</label>
            <label style={{ color: "rgb(50, 220, 199)" }}>********</label>
            <div className="btn" onClick={(e) => { onClickHandler({ uid }) }}><span>비밀번호 변경</span></div>
          </div>
          <div id="user">
            <label>닉네임</label>
            <label style={{ color: "rgb(50, 220, 199)" }}>{uName}</label>
            <div className="btn"><span>닉네임 변경</span></div>
          </div>
          <div id="user">
            <label>서비스 로그아웃</label>
            <div className="btn" onClick={(e) => { onClickHandler({ uid }, "/") }}><span>로그아웃</span></div>
          </div>
          <div id="removeUser">
            <label onClick={(e) => { onClickRemoveUserHandler({ uid }, { uName }) }}>회원탈퇴</label>
          </div>
        </div>
      </div>
      <label>내가 등록한 식단</label>
        {
          isEmptyItem ? 
          <div className="UserRegiInfoForm">
            {
              inputData.map((vv, index) => {
                if(index > 0) {
                  return <MainDietSearchResultBox d_no={vv.d_no} diet_name={vv.diet_name} imgd={vv.imgd} style={{ display: "none" }}></MainDietSearchResultBox>
                }
              })
            }
          </div>
          : <div className="UserRegiInfoForm" style={boxStyle}><label>등록한 식단이 없습니다</label></div>
        }
      <div className="pageBtn" style={{ paddingBottom: "40px" }}>
        <div id="circle"></div>
      </div>
    </div>
  )
}

function MainDietSearchResultBox(props) {
  const onClickDeleteHandler = e => {
    Swal.fire({
      icon: 'question',
      title: '해당 식단을 삭제하시겠습니까?',
      // timer: 100000,
    })
  }

  const onClickEditHandler = e => {
    window.location.href = "/diet_edit";
  }
  return (
    <>
      <div className="MyPageRecipeRankBox">
        <div className="myPageBtnForm">
          <div className="editBtn" onClick={(e) => {onClickEditHandler(e)}}>
            <i className="fas fa-pen"></i>
          </div>
          <div className="removeBtn" onClick={(e)=> {onClickDeleteHandler(e)}}>
            <i className="fas fa-times"></i>
          </div>
        </div>
        <div className="MainRecipeImg" onClick={(e) => { viewDietClick(props.d_no, props.diet_name)}}>
          <img src={props.imgd} alt="Blob URL Image" width={320} height={240}></img>
        </div>
        <div className="MainRecipeName">
          <label>No. {props.d_no}</label>
          <label>{props.diet_name}</label>
        </div>
      </div> 
    </>
  )
}

const viewDietClick = (d_no, diet_name, e) => {
  console.log(d_no + ", " + diet_name);

  if (d_no != null && diet_name != null) {
    window.location.href = "/detail_diet?d_no=" + d_no + "&diet_name=" + diet_name;
  } else {
    window.location.href = "/";
  }
}

const onClickHandler = (userId, route) => {
  const param = Object.values(userId);
  if (userId) {
    // console.log(Object.values(userId));
    document.location.href = "/findPwLast?id=" + param;
  }

  if (userId && route) {
    if (route == "/") {
      sessionStorage.clear();
      document.location.href = "/";
    }
  }
}

const onClickRemoveUserHandler = (userName) => {
  const user = Object.values(userName);
  const isChecked = window.confirm(user + "님의 회원탈퇴를 진행하시겠습니까?");

  if (isChecked) {
    alert("회원탈퇴 진행");
  }
  console.log(isChecked);
}

export default MyPage;