import { BrowserRouter, Routes, Route, Link } from 'react-router-dom'
import Refrigerator_main from './Refrigerator_main';
import Login from './Login';
import Home from "./Main";
import Recipe from "./Recipe";
import DailyMeals from './DailyMeals';
import MyFridge from './MyFridge'
import MyPage from './MyPage';
import Diet from "./Diet";
import JoinFirst from "./JoinFirst";
import Join from "./Join";
import JoinLast from "./JoinLast";
import FindId from "./FindId";
import FindPw from "./FindPw";
import FindPwLast from "./FindPwLast"
import Dietcreate from './DietCreate';
import DietEdit from './DietEdit';
import DetailDietView from './DetailDietView'
import DetailRecipeView from './DetailRecipeView'
import "./css/reset.css"
// 메인
function App() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path='/' element={<Home />} />
        <Route path='/login' element={<Login />} />
        <Route path='/recipe' element={<Recipe />} />
        <Route path='/daily_meals' element={<DailyMeals />} />
        <Route path='/mypage' element={<MyPage />} />
        <Route path="/diet" element={<Diet />} />
        <Route path='/detail_diet' element={<DetailDietView/>} />
        <Route path="/diet_create" element={<Dietcreate />} />
        <Route path="/diet_Edit" element={<DietEdit />} />
        <Route path='/Refrigerator' element={<Refrigerator_main />} />
        <Route path='/myfridge' element={<MyFridge />} />
        <Route path='/detail_recipe' element={<DetailRecipeView />} />
        <Route path='/joinFirst' element={<JoinFirst />} />
        <Route path='/join' element={<Join />} />
        <Route path='/joinLast' element={<JoinLast />} />
        <Route path='/findId' element={<FindId />} />
        <Route path='/findPw' element={<FindPw />} />
        <Route path='/findPwLast' element={<FindPwLast />} />
        <Route path='/find_recipe1' element={<Dietcreate />} />
        <Route path='/find_recipe2' element={<Dietcreate />} />
        <Route path='/find_recipe3' element={<Dietcreate />} />


      </Routes>
    </BrowserRouter>
  );
}

export default App;
