import Header from "./Header";
import Footer from "./Footer";
import Layout from './Menubar';
import Kan from './Kan';
import Select from './Select'
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Refrigerator_main() {

  // 요청받은 정보를 담아줄 변수 선언
  const [testStr, setTestStr] = useState('');

  // 변수 초기화
  function callback(str) {
    setTestStr(str);
  }

  // 첫 번째 렌더링을 마친 후 실행
  useEffect(
    () => {
      axios({
        url: '/Refrigerator',
        method: 'GET'
      }).then((res) => {
        callback(res.data);
      })
    }, []
  );

  return (
    <div className="Refrigerator">
      <Header />
      <Layout />
      <Kan />
      <Select />
      <Footer />
      {testStr}
    </div>
  );
}

export default Refrigerator_main;