import "./css/joinLast.css"

function JoinLast() {
    return (
        <div className="JoinLastForm">
            <div className="navLogo" onClick={(e) => {clickBtn("/", e)}}>
                <span className="navLogo">CAPSTONE </span>
                <span className="navLogo" id="LogoColor">DESIGN</span>
            </div>
            <div className="JoinFirstContent">
                <span>서비스 가입이 완료되었습니다.</span>
                <span>홈에서 로그인 해주세요.</span>
            </div>
            <div className="JoinNextBtn" onClick={(e) => {clickBtn("/", e)}}>홈으로</div>
        </div>
    )
}

const clickBtn = (params, e) => {
    console.log(params);
    if(params == "/") {
        document.location.href = "/";
    }
};

export default JoinLast;