import "./css/findPwLast.css"
import React, { useState, useEffect } from 'react';

function FindPwLast() {
    const params = new URLSearchParams(window.location.search);
    const userId = params.get('id');
    console.log(params, userId);
    // const [email, setInputEmail] = useState("");
    let id = '';
    const [pass, setInputPw] = useState("");
    const [pass2, setInputPw2] = useState("");

    // const handleInputEmail = (e) => {
	// 	setInputEmail(e.target.value);
	// };

    if(userId) {
        id = userId;
    }

    const handleInputPw = (e) => {
        setInputPw(e.target.value);
    };
    
    const handleInputPw2 = (e) => {
        setInputPw2(e.target.value);
    };

    return (
        <div className="FindPwLastForm">
			<div className="navLogo" onClick={(e) => {clickBtn("/", e)}}>
        		<span className="navLogo">CAPSTONE </span>
        		<span className="navLogo" id="LogoColor">DESIGN</span>
      		</div>
			<div className="FindPwTitle"><span>비밀번호 재설정</span></div>
			<div className="InputId">
                {/* 이전 페이지에서 이메일 주소 값 가져와서 집어넣음 */}
	  			<input type="text" path="email" placeholder="이메일" id="email" name="email" style={{color: "rgb(50, 220, 199)"}} value={id} readOnly />
	  		</div>
            <div className="InputPw">
	  	        <input type="password" path="pass" placeholder="비밀번호" id="pass" name="pass" value={pass} onChange={handleInputPw} />
	        </div>
	        <div className="InputPwCheck">
	  	        <input type="password" path="pass2" placeholder="비밀번호 확인" id="pass2" name="pass2" value={pass2} onChange={handleInputPw2} />
	        </div>
			<div className="FindPwNextBtn" onClick={(e) => {clickBtn("success", e)}}>비밀번호 변경</div>
        </div>
    );
}

const clickBtn = (params, e) => {
    console.log(params);
    if(params == "/") {
        document.location.href = "/";
    } else if(params == "success") {
        alert("비밀번호가 변경되었습니다.");
        document.location.href = "/"
    }
};

export default FindPwLast;