import Header from "./Header";
import Footer from "./Footer";
import "./css/reset.css"
import "./css/diet.css"
import { useEffect, useState } from 'react';

function Diet() {
  let [diet_name, a] = useState(['식단01', '식단02', '식단03', '식단04']);
  let [hash_tag, b] = useState(['#해시태그1', '#해시태그2', '#해시태그3']);
  let [keyWord, c] = useState(['키워드', '키워드', '키워드', '키워드']);
  let id = null;

  return (
    <div className="Diet">
      <Header />
      <DietContainer></DietContainer>
      <Footer />
    </div>

  );
}

function DietContainer() {
  const [message, setMessage] = useState("")
  useEffect(() => {
    fetch("/diet")
      .then(res => res.text())
      .then(m => setMessage(m))
  }, [])
  return (
    <div className="content">
      <div className="regi">
        <span id="regi_title">나만의 3끼 식단 만들기</span>
        <span id="regisub_title">식단을 만들어 다른 이용자와 공유해보세요</span>
        <div id="btn" className="btn" onClick={clickBtn}>
          <span>식단 만들기</span><i className="fas fa-angle-right" id="arrow"></i>
        </div>
      </div>
      {message}
    </div>
  );
}

const clickBtn = () => {
  let id = "test";

  if (id != null) {
    window.location.href = "/diet/diet_create";
  } else {
    alert("로그인이 필요한 서비스입니다.");
    window.location.href = "/diet";
  }
}

export default Diet;