import "./css/recipe.css";
import Header from "./Header";
import Footer from "./Footer";
import React, { useState, useEffect, useRef } from "react";
import axios from "axios";

function Recipe() {
    return (
        <div className="RecipeForm">
            <Header />
            <RecipeContent />
            <Footer />
        </div>
    )
}

const viewRecipeClick = (recipe_id, recipe_nm_ko, e) => {
    console.log(recipe_id + ", " + recipe_nm_ko);

    if (recipe_id != null && recipe_nm_ko != null) {
        window.location.href = "/detail_recipe?recipe_id=" + recipe_id + "&recipe_nm_ko=" + recipe_nm_ko;
    } else {
        window.location.href = "/";
    }
}

function MainRecipeSearchResultBox(props) {
    return (
        <div className="MainRecipeRankBox" onClick={(e) => {viewRecipeClick(props.recipe_id, props.recipe_nm_ko, props.img)}}>
            <div className="MainRecipeImg"><img src={props.img} alt="Blob URL Image" width={320} height={240}></img></div>
            <div className="MainRecipeName">
                <span>No. {props.recipe_id}</span>
                <span>{props.recipe_nm_ko}</span>
            </div>
        </div>
    )
}

function RecipeContent() {
    const [isEmptyItem, setIsEmptyItem] = useState(false);
    const boxStyle = {
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    };

    /** useEffect로 페이지가 랜더링 될때마다 키워드를 랜덤으로 가져와서 세팅함 */
    let [keyword, setKeyword] = useState([{
        keyword1: '면',
        keyword2: '파스타',
        keyword3: '조림',
        keyword4: '구이',
        keyword5: '고기',
        keyword6: '샐러드',
        keyword7: '국',
        keyword8: '밥',
    }]);

    const [recipeResults, setRecipeResults] = useState([{
        recipe_id : '',
        recipe_nm_ko : '',
        img : '',

    }])


    const [search, setSearch] = useState('');

    const [html, sethtml] = useState('');

    const onChangeSearch = (e) => {
        let inputValue = e.target.value;

        e.preventDefault();
        setSearch(inputValue);
        // setSearch(inputValue);
        console.log("Search: " + inputValue);
        console.log("setSearch: " + search);
    }

    const tag = (e) => {
        axios
        .post("/search1", {
            recipe_nm_ko: e
        })
        .then((res) => {

            const _inputData = res.data.map((rowData) => (
                {
                    recipe_id: rowData.recipe_id,
                    recipe_nm_ko: rowData.recipe_nm_ko,
                    img: rowData.img,
                })
            )
            console.log("_inputData : " + JSON.stringify(_inputData))
            console.log("_inputData : " + _inputData.length)

            // if(_inputData.length==0){sethtml("검색결과가 없습니다.")}
            // else{sethtml("")}

            setIsEmptyItem(false);
            if(_inputData.length > 0) {
                setIsEmptyItem(true);
            }
            setRecipeResults(recipeResults.concat(_inputData))

        })
    console.log(recipeResults)
    recipeResults.splice(1)

    }

    const pressEnter = (e) => {
        let inputKeyword = document.getElementById('inputKeyword').value;

        if (e.keyCode == 13) {
            onChangeSearch(e);
            //엔터키가 눌릴 때
            if (e.keyCode == 13) {
                /** 
                 * 전체 검색 태그 0 -> x
                 * 레시피 검색 태그 1 
                 * 식단 검색 태그 2
                 * 각각의 태그 번호와 inputKeyword에 있는 값을 가지고 검색 구현
                 */

                if (inputKeyword.length == 0) {
                    // alert(", 검색어가 입력되지 않았습니다.");
                } else {
                    // alert(", 검색어: " + inputKeyword);
                    axios
                        .post("/search1", {
                            recipe_nm_ko: inputKeyword
                        })
                        .then((res) => {

                            const _inputData = res.data.map((rowData) => (
                                {
                                    recipe_id: rowData.recipe_id,
                                    recipe_nm_ko: rowData.recipe_nm_ko,
                                    img: rowData.img,
                                })
                            )
                            console.log("_inputData : " + JSON.stringify(_inputData))
                            console.log("_inputData : " + _inputData.length)

                            // if(_inputData.length==0){sethtml("검색결과가 없습니다.")}
                            // else{sethtml("")}

                            setIsEmptyItem(false);
                            if(_inputData.length > 0) {
                                setIsEmptyItem(true);
                            }
                            setRecipeResults(recipeResults.concat(_inputData))

                        })
                    console.log(recipeResults)
                    recipeResults.splice(1)

                }

            }
            console.log("input : ", inputKeyword);
        }
    }

    return (
        <div className="RecipeContent">
            <div className="RecipeSearchForm">
                <span id="searchTitle">레시피 찾아보기</span>
                <span id="searchSubTitle">키워드를 이용해 검색하거나 레시피 이름을 직접 입력해 검색 해보세요</span>
                <div className="RecipeSearchBox">
                    <div className="RecipeInputKeyword">
                        <i className="fas fa-search" id="searchIcon"></i>
                        <input type="text" id="inputKeyword" placeholder="키워드를 입력해주세요" onKeyDown={(e) => { pressEnter(e) }}></input>
                    </div>
                </div>
                <div className="RecipeKeywordForm">
                    <div className="RecipeKeywordList" id="all" value="all"><span>전체</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword1} onClick={(e) => tag(keyword[0].keyword1)}><span>{keyword[0].keyword1}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword2} onClick={(e) => tag(keyword[0].keyword2)}><span>{keyword[0].keyword2}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword3} onClick={(e) => tag(keyword[0].keyword3)}><span>{keyword[0].keyword3}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword4} onClick={(e) => tag(keyword[0].keyword4)}><span>{keyword[0].keyword4}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword5} onClick={(e) => tag(keyword[0].keyword5)}><span>{keyword[0].keyword5}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword6} onClick={(e) => tag(keyword[0].keyword6)}><span>{keyword[0].keyword6}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword7} onClick={(e) => tag(keyword[0].keyword7)}><span>{keyword[0].keyword7}</span></div>
                    <div className="RecipeKeywordList" id="value" value={keyword[0].keyword8} onClick={(e) => tag(keyword[0].keyword8)}><span>{keyword[0].keyword8}</span></div>
                </div>
                {
                    isEmptyItem === false ? <div className="RecipeSearchResultForm" style={boxStyle}><span>검색 결과가 없습니다</span></div> :
                    <div className="RecipeSearchResultForm">
                        {recipeResults.map((v,index) => {
                            if(index > 0) { 
                                return <MainRecipeSearchResultBox recipe_id={v.recipe_id} recipe_nm_ko={v.recipe_nm_ko} img={v.img}></MainRecipeSearchResultBox>
                            }
                        })}
                    </div>
                }
                {/* <div className="RecipeSearchResultForm">
                    검색결과가 없으면
                    <span id="notSearchResult">{html}</span>
                    {recipeResults.map((v,index) => {if(index > 0){ return <MainRecipeSearchResultBox recipe_id={v.recipe_id} recipe_nm_ko={v.recipe_nm_ko} img={v.img}></MainRecipeSearchResultBox>}})}
                </div> */}
            </div>
        </div>
    )
}

const pressEnter = (e) => {
    let inputKeyword = document.getElementById('inputKeyword').value;

    //엔터키가 눌릴 때
    if (e.keyCode == 13) {
        if (inputKeyword.length == 0) {
            alert("검색어가 입력되지 않았습니다.");
        } else {
            onChangeSearch(e);
            alert("검색어: " + inputKeyword);
        }
    }
}

export default Recipe;