import "./css/joinFirst.css";

function JoinFirst() {
    return (
        <div className="JoinFirstForm">
            <div className="navLogo" onClick={(e) => {clickBtn("/", e)}}>
                <span className="navLogo">CAPSTONE </span>
                <span className="navLogo" id="LogoColor">DESIGN</span>
            </div>
            <div className="JoinFirstContent">
                <span>서비스 가입을 시작합니다.</span>
                <span>회원가입을 위해 가입 방식을 선택하세요.</span>
            </div>
            <div className="JoinNextBtn" onClick={(e) => {clickBtn("Join", e)}}>이메일로 가입</div>
            <div className="hr-sect">또는</div>
            <div className="social">
                <ul className="social-login">
                    <li className="list-item-img">
                        <a href="#" id="google-login">
                        <div className="social-icon">
                            <img className="google-icon" src={`${process.env.PUBLIC_URL}/btn_google_signin_light_normal_web@2x.png`} alt="" />
                        </div>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
    )
}

const clickBtn = (params, e) => {
    console.log(params);
    if(params == "/") {
        document.location.href = "/";
    } else if(params == "Join") {
        document.location.href = "/join";
    }
};

export default JoinFirst;