import Header from "./Header";
import Footer from "./Footer";
import "./css/reset.css"
import "./css/detailRecipeView.css"
import { useState } from 'react';
import React, { useEffect } from "react";
import axios from "axios";
import ReactPlayer from "react-player";

function DetailRecipeView() {
    const params = new URLSearchParams(window.location.search);

    let recipe_id = params.get('recipe_id');
    let recipe_nm_ko = params.get('recipe_nm_ko');

    useEffect(() => {
        console.log('레시피 번호: ' + recipe_id + ' 레시피 이름: ' + recipe_nm_ko);
    });
    return (
        <div className="detailRecipe">
            <Header />
            <DetailView recipe_id={recipe_id} recipe_nm_ko={recipe_nm_ko}></DetailView>``
            <Footer />
        </div>
    );
}

function DetailView(props) {
    const params = new URLSearchParams(window.location.search);

    let recipe_id = params.get('recipe_id');
    let recipe_nm_ko = params.get('recipe_nm_ko');

    const [inputData, setInputData] = useState([{
        recipe_id: null,
        recipe_nm_ko: null,
        sumry: null,
        nation_nm: null,
        ty_nm: null,
        cooking_time: null,
        calorie: null,
        qnt: null,
        level_nm: null,
        img: null,
        like_num: null,
        cooking_no: null,
        irdnt_nm: null,
        irdnt_cpcty: null,
        irdnt_ty_nm: null,
        cooking_dc: null,
        stre_step_image_url: null,
        irdnt:null,
        url:null,


    }])

    // 첫 번째 렌더링을 마친 후 실행
    useEffect((es) => {
        const fetchData = async () => {
            try {

                const res = await axios.get('/detail_recipe?recipe_id='+recipe_id)
                const _inputData = await res.data.map((rowData) => (
                    // setLastIdx(lastIdx + 1),
                    {
                        recipe_id: rowData.recipe_id,
                        recipe_nm_ko: rowData.recipe_nm_ko,
                        sumry: rowData.sumry,
                        nation_nm: rowData.nation_nm,
                        ty_nm: rowData.ty_nm,
                        cooking_time: rowData.cooking_time,
                        calorie: rowData.calorie,
                        qnt: rowData.qnt,
                        level_nm: rowData.level_nm,
                        img: rowData.img,
                        like_num: rowData.like_num,
                        cooking_no: rowData.cooking_no,
                        irdnt_nm: rowData.irdnt_nm,
                        irdnt_cpcty: rowData.irdnt_cpcty,
                        irdnt_ty_nm: rowData.irdnt_ty_nm,
                        cooking_dc: rowData.cooking_dc,
                        stre_step_image_url: rowData.stre_step_image_url,
                        irdnt: rowData.irdnt,
                        url: rowData.url,


                    })

                )

                setInputData(inputData.concat(_inputData))
            } catch (e) {
                console.log(e)

            }
        }
        fetchData();
    }, [])

    let recNo = props.cooking_no;
    let recName = props.recipe_nm_ko;

    let recDes = [inputData[1]?.sumry];
    let recClass = [inputData[1]?.nation_nm];
    let recIngredient = [inputData[1]?.irdnt];
    let recImage = [inputData[1]?.img];
    let url = inputData[1]?.url;
    var num = inputData.length
    let stepImage = []
    // let recCookingTitle = []
    let recCookingDes = []
    for (var i = 2; i < num; i++) {
        if(inputData[i]?.stre_step_image_url != null){stepImage.push(inputData[i]?.stre_step_image_url)}
        recCookingDes.push(inputData[i]?.cooking_dc)
    }

    // let stepImage = [inputData[2]?.stre_step_image_url, inputData[3]?.stre_step_image_url, inputData[4]?.stre_step_image_url, inputData[5]?.stre_step_image_url, inputData[6]?.stre_step_image_url, inputData[7]?.stre_step_image_url];//과정 개수에 따라 늘어나야됨
    let recCookingTitle = ["1. 조리순서 1번 타이틀", "2. 조리순서 2번 타이틀", "3. 조리순서 3번 타이틀", "4. 조리순서 4번 타이틀", "5. 조리순서 5번 타이틀", "6. 조리순서 6번 타이틀", "7. 조리순서 7번 타이틀", "8. 조리순서 8번 타이틀", "9. 조리순서 9번 타이틀", "10. 조리순서 10번 타이틀", "11. 조리순서 11번 타이틀", "12. 조리순서 12번 타이틀", "13. 조리순서 13번 타이틀", "14. 조리순서 14번 타이틀", "15. 조리순서 15번 타이틀", "16. 조리순서 16번 타이틀", "17. 조리순서 17번 타이틀", "18. 조리순서 18번 타이틀", "19. 조리순서 19번 타이틀", "20. 조리순서 20번 타이틀", "21. 조리순서 21번 타이틀", "22. 조리순서 22번 타이틀"]//과정 개수에 따라 늘어나야됨
    // let recCookingDes = [inputData[2]?.cooking_dc, inputData[3]?.cooking_dc, inputData[4]?.cooking_dc, inputData[5]?.cooking_dc, inputData[6]?.cooking_dc, inputData[7]?.cooking_dc];//과정 개수에 따라 늘어나야됨


    let sideDishImg = ["이미지 01", "이미지 02", "이미지 03"];

    console.log(inputData);

    /*
     * recipeNo랑 recipeName을 가지고 DB 데이터 조회 코드 작성
     */

    function CookingOrder(props) {
        return (
            <div className="cookingOrder">
                <div className="cookingDes">
                    <span id="cookingOrder">{props.recCookingTitle}</span>
                    <span id="cookingDescription">{props.recCookingDes}</span>
                </div>
                <div className="cookingImg">
                    <span id="cookingImage"><img src={props.stepImage} alt="Blob URL Image" width={420} height={420}/></span>
                </div>
            </div>
        );
    }

    function step(){
        var num = inputData.length - 2
        var arr = [];
        for (var i = 2; i < num+2; i++) {
            arr.push(<CookingOrder stepImage={stepImage[i-2]} recCookingTitle={recCookingTitle[i-2]} recCookingDes={recCookingDes[i-2]}></CookingOrder>)
        }
        return arr
    }

    return (
        <div className="detailRecipeView">
            <div className="recipeInfo">
                <div id="recipeImg"><img src={recImage} alt="Blob URL Image" width={420} height={420}></img></div>
                <div id="recipe">
                    <div className="recNo">
                        {/* <span>No. </span>
                        <span id="no">{recNo}</span> */}
                        <span id="name">{recName}</span>
                    </div>
                    <div className="recDes">
                        <span>[레시피 설명]</span>
                        <span id="description">{recDes}</span>
                    </div>
                    <div className="recipeClass">
                        <span>[분류]</span>
                        <span id="recClass">{recClass}</span>
                    </div>
                    <div className="recipeIngredient">
                        <span>[재료 및 계량]</span>
                        <span id="recIngredient">{recIngredient}</span>
                    </div>
                </div>
            </div>
            <div className="cookingInfo">
                <div className="recTitle">
                    <span id="name">[ {recName} 조리 순서 ]</span>
                </div>
                {step()}
            </div>

            <ReactPlayer
                    className='react-player'
                    url={url}    // 플레이어 url
                    width='800px'         // 플레이어 크기 (가로)
                    height='500px'        // 플레이어 크기 (세로)
                    playing={false}        // 자동 재생 on
                    muted={true}          // 자동 재생 on
                    controls={true}       // 플레이어 컨트롤 노출 여부
                    light={false}         // 플레이어 모드
                    pip={false}            // pip 모드 설정 여부

                />
        </div>
    );
}

function SideDish(props) {
    return (
        <div className="sideDish">
            <span>{props.sideDishImg}</span>
        </div>
    );
}

export default DetailRecipeView;