import "./css/myFridge.css";
import Header from "./Header"
import Footer from "./Footer"
import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Swal from "sweetalert2";

function MyFridge() {
    return(
        <div className="MyFridgeForm">
            <Header />
            <MyFridgeContent />
            <Footer />
        </div>
    )
}

function MyFridgeContent() {
    // console.log("UserID: " + sessionStorage.getItem("id"))
    let [optionCategory1, setOptionCategory1] = useState(['0']);
    let [optionCategory2, setOptionCategory2] = useState(['0']);
    const [addItem, setAddItem] = useState(false);
    let isEmptyItem = false;
    let [isSelectedItem, setIsSelectedItem] = useState(false);
    let [isClickCheck, setIsClickCheck] = useState(false);

    let uid = sessionStorage.getItem("id");

    // console.log("테스트" + uid)

    const [inputData, setInputData] = useState([{
        ingredientName: '',
        numb: '',
        shelflife: '',
        img: '',
        writer_date: '',

        
    }])

    const[txt, setTxt] = useState([{
        name: '',
        mount: '',
        date: '',
        img: '',
    }]);

    console.log("txt에 추가된것들 : " + JSON.stringify(txt))

    var txtData = [];

    for(var i = 1; i<txt.length; i++){
                
        // console.log("클릭한 식재료 이름 : " + i + "번"+txt[i].name);
        txtData.push(txt[i].name)
    }

    console.log("클릭한 식재료 이름 : " + txtData);

    function GetIngredientItem(props) {
        const[fontColor, setFontColor] = useState();
    
        const date = new Date();
        const year = date.getUTCFullYear();
        const month = date.getUTCMonth() + 1;
        const day = date.getUTCDate();
        let today = String(year) + "-" + String(month) + "-" + String(day);
        // let today = "2022-10-31"
        let timeLimit = props.date;
        // console.log(year + " " + month + " " + day);
        // console.log(today);
    
        const getDateDiff = (d1, d2) => {
            const date1 = new Date(d1);
            const date2 = new Date(d2);
            
            // const diffDate = date1.getTime() - date2.getTime();
            const diffDate = date2.getTime() - date1.getTime();
            
            return Math.ceil(diffDate / (1000 * 60 * 60 * 24)+1); // 밀리세컨 * 초 * 분 * 시 = 일
        }
          
        const getLimitDay = getDateDiff(today, timeLimit);
        // console.log(getLimitDay);
        const dateRef = useRef("");
    
        useEffect(() => {
            if(getLimitDay <= 0) {
                setFontColor("rgb(193, 193, 193)");
            } else if(getLimitDay >= 0 && getLimitDay < 8) {
                setFontColor("rgb(255, 59, 48)");
    
                
            } else if(getLimitDay > 8 && getLimitDay < 31) {
                setFontColor("rgb(255, 149, 0)");
            } else if(getLimitDay >= 31) {
                setFontColor("rgb(52, 199, 89)");
            }
        }, [setFontColor]);
    
        if(dateRef.current.innerText <= 0) {
            console.log(dateRef.current.innerText = "유통기한 만료");
        }
    
        if(dateRef.current.innerText == 1) {
            let returnValue = "남은 유통기한 1일 미만";
    
            console.log(dateRef.current.innerText = returnValue)
        }
    
        if(dateRef.current.innerText > 0) {
            let returnValue = "남은 유통기한 " + dateRef.current.innerText + "일";
            
            console.log(dateRef.current.innerText = returnValue)
        }
        const len = txt.length;
        const onClickItemHandler = (key, e) => {
            // console.log("Click한 item 이름: " + key);
            
            console.log("Click후 추가된 item 배열 길이: " + len);
            if(len<=10){
                setIsSelectedItem(true);
                const s = {
                    name: key[0],
                    mount: key[1],
                    date: key[2],
                    img: key[3],
                }
                // console.log("Click한 item 값: " + JSON.stringify(s));
                setTxt(txt.concat(s))
                
                // console.log(txt);
                
                // console.log("Click한 item 이름: " + txt);
                return
            }else{alert("10개 초과 불가")}
        }
        // for(let i; i <= txt.length; i++){console.log("Click후 추가된 item 이름: " + txt[i]);}
        // console.log("Click후 추가된 item 이름: " + JSON.stringify(txt[1]));
        const onClickItemDeleteHandler = (key, e) => {
            console.log("key: " + key + ", 삭제");
            let uid = sessionStorage.getItem("id");
            Swal.fire({
                icon: 'warning',
                title: '삭제',
                text: '삭제 하시겠습니까?',
                showCancelButton: true, // cancel버튼 보이기. 기본은 원래 없음
                confirmButtonColor: '#3085d6', // confrim 버튼 색깔 지정
                cancelButtonColor: '#d33', // cancel 버튼 색깔 지정
                confirmButtonText: 'YES', // confirm 버튼 텍스트 지정
                cancelButtonText: 'NO', // cancel 버튼 텍스트 지정
                // timer: 100000,
            }).then((q) => {
              if(q.isConfirmed){
                axios
                .post("http://localhost:8080/delIngredient", {
                    email: uid,
                    ingredientName: key,
        
                }).then((res) => {
                    if (res.data == 1) {
                        Swal.fire({
                            icon: 'success',
                            title: '삭제됐습니다',
                            // timer: 100000,
                        }).then((q) => {
                          if(q.isConfirmed){
                            // 작업 완료 되면 페이지 이동(새로고침)
                            document.location.href = "/myfridge";
                          }
                        }
                        );
                    }
                }).catch(function (error) {
                    console.log(error)
                })
              }else if(q.isDismissed){
                document.location.href = "/myfridge";
              }
            }
            );
        }
    
        const onClickItemEditHandler = (key, e) => {
            console.log("수정할 item 이름: " + key + ", 수정");
            let uid = sessionStorage.getItem("id");
    
            // axios
            // .post("http://localhost:8080/updateIngredient", {
            //     email: uid,
            //     ingredientName: key,
    
            // }).then((res) => {
            //     if (res.data == 1) {
            //         alert("update ok");
            //         window.location.replace("/myfridge")
            //     }
            // }).catch(function (error) {
            //     console.log(error)
            // })
        }
    
        return (
            <>
                <div className="getIngredientItemForm">
                    <div className="btnForm">
                        <div className="editBtn" onClick={(e) => {onClickItemEditHandler(props.name, e)}}>
                            <i className="fas fa-pen"></i>
                        </div>
                        <div className="deleteBtn" onClick={(e) => {onClickItemDeleteHandler(props.name, e)}}>
                            <i className="fas fa-times"></i>
                        </div>
                    </div>
                    <div className="getIngredientItem" onClick={(e) => {onClickItemHandler([props.name, props.mount, props.date, props.img], e)}}>
                        <div id="img">
                            <img src={props.img} width={100} height={100}></img>
                        </div>
                        <div className="ingredientInfoForm">
                            <div className="ingredientInputForm">
                                <div id="shelfLifeLimitForm">
                                    <label>유통기한</label>
                                    <span id="time">~</span><span id="time">{props.date}</span>
                                </div>
                                <div id="ingredientNameForm">
                                    <label>{props.name}</label>
                                </div>
                                <div id="timeLimitForm">
                                    <label id="dateLabel" style={{color: fontColor}} ref={dateRef}>{getLimitDay}</label>
                                </div>
                            </div>
                            <div className="editForm">
                                <div className="ingredientCount">
                                    <label>수량</label>
                                    <span>{props.mount}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
    
    function ExpiryItem(props) {
        const[fontColor, setFontColor] = useState();
    
        const date = new Date();
        const year = date.getUTCFullYear();
        const month = date.getUTCMonth() + 1;
        const day = date.getUTCDate();
        const dateRef = useRef("");
    
        let today = String(year) + "-" + String(month) + "-" + String(day);
        let timeLimit = props.date;
    
        const getDateDiff = (d1, d2) => {
            const date1 = new Date(d1);
            const date2 = new Date(d2);
            
            // const diffDate = date1.getTime() - date2.getTime();
            const diffDate = date2.getTime() - date1.getTime();
            
            return Math.ceil(diffDate / (1000 * 60 * 60 * 24)+1); // 밀리세컨 * 초 * 분 * 시 = 일
        }
          
        const getLimitDay = getDateDiff(today, timeLimit);
    
        useEffect(() => {
            if(getLimitDay <= 0) {
                setFontColor("rgb(193, 193, 193)");
            } else if(getLimitDay > 0 && getLimitDay < 8) {
                setFontColor("rgb(255, 59, 48)");
            } else if(getLimitDay > 8 && getLimitDay < 31) {
                setFontColor("rgb(255, 149, 0)");
            } else if(getLimitDay >= 31) {
                setFontColor("rgb(52, 199, 89)");
            }
        }, [setFontColor]);
    
        if(dateRef.current.innerText <= 0) {
            console.log(dateRef.current.innerText = "유통기한 만료");
        }
    
        if(dateRef.current.innerText == 1) {
            let returnValue = "남은 유통기한 1일 미만";
    
            console.log(dateRef.current.innerText = returnValue)
        }
    
        if(dateRef.current.innerText > 0) {
            let returnValue = "남은 유통기한 " + dateRef.current.innerText + "일";
            
            console.log(dateRef.current.innerText = returnValue)
        }
        const len = txt.length;
        const onClickItemHandler = (key, e) => {
            // console.log("Click한 item 이름: " + key);
            
            // console.log("Click후 추가된 item 배열 길이: " + key);
            if(len<=10){
                const s = {
                    name: key[0],
                    mount: key[1],
                    date: key[2],
                    img: key[3],
                }
                // console.log("Click한 item 값: " + JSON.stringify(s));
                setTxt(txt.filter(s => s.name !== key[0]))
                
                // console.log(txt);
                
                // console.log("Click한 item 이름: " + txt);
            }else{alert("10개 초과 불가")}
            
    
        }
    
        return (
            <div className="ExpiryItemForm">
                <div className="getIngredientItem" onClick={(e) => {onClickItemHandler([props.name, props.mount, props.date, props.img], e)}}>
                    <div id="img">
                        <img src={props.img} width={100} height={100}></img>
                    </div>
                    <div className="ingredientInfoForm">
                        <div className="ingredientInputForm">
                            <div id="shelfLifeLimitForm">
                                <span>유통기한</span>
                                <span id="time">~</span><span id="time">{props.date}</span>
                            </div>
                            <div id="ingredientNameForm">
                                <span>{props.name}</span>
                            </div>
                            <div id="timeLimitForm">
                                <label id="dateLabel" style={{color: fontColor}} ref={dateRef}>{getLimitDay}</label>
                            </div>
                        </div>
                        <div className="editForm">
                            <div className="ingredientCount">
                                <span>수량</span>
                                <span>{props.mount}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    
    const onClickHandler = (option1, option2, e) => {
        /** 
         * 각 옵션에 해당하는 나의 냉장고 재료값을 DB에서 조회 후 출력
         */
        alert(JSON.stringify(inputData))
        if(option1 == 0) {
            if(option2 == 0) {
                alert("전체: " + option1 + ", 등록순: " + option2);
            } else if(option2 == 1) {
                alert("전체: " + option1 + ", 남은 기한순: " + option2);
            }
        } else if(option1 == 1) {
            if(option2 == 0) {
                alert("냉장: " + option1 + ", 등록순: " + option2);
            } else if(option2 == 1) {
                alert("냉장: " + option1 + ", 남은 기한순: " + option2);
            }
        } else if(option1 == 2) {
            if(option2 == 0) {
                alert("냉동: " + option1 + ", 등록순: " + option2);
            } else if(option2 == 1) {
                alert("냉동: " + option1 + ", 남은 기한순: " + option2);
            }
        } else if(option1 == 3) {
            if(option2 == 0) {
                alert("실온: " + option1 + ", 등록순: " + option2);
            } else if(option2 == 1) {
                alert("실온: " + option1 + ", 남은 기한순: " + option2);
            }
        } else {
            alert("조회 실패");
        }
    }

    const run = () =>{
        axios
    .post("/myfridge", {
        email: uid
    })
    .then((res) => {

        const _inputData = res.data.map((rowData) => (
            {
                storeinfo: rowData.storeinfo,
                ingredientName: rowData.ingredientName,
                numb: rowData.numb,
                shelflife: rowData.shelflife,
                img: rowData.img,
                writer_date: rowData.writer_date,
            })
        )
        setInputData(inputData.concat(_inputData))
    })  
    }
    useEffect(run, [])
    
    // console.log("읽어온거 : " + JSON.stringify(inputData))

    // const expiryItem = [
    //     {
    //         itemNo: 4,
    //         img: "tomato.jpg",
    //         date: "2022-11-08",
    //         name: "연어",
    //         mount: "10개",
    //     },
    //     {
    //         itemNo: 5,
    //         img: "salmon.jpg",
    //         date: "2022-11-11",
    //         name: "연어",
    //         mount: "2개",
    //     },
    // ]

    const viewRecipeClick = (recipe_id, recipe_nm_ko, e) => {
        console.log(recipe_id + ", " + recipe_nm_ko);
    
        if (recipe_id != null && recipe_nm_ko != null) {
            window.location.href = "/detail_recipe?recipe_id=" + recipe_id + "&recipe_nm_ko=" + recipe_nm_ko;
        } else {
            window.location.href = "/";
        }
    }
    
    function MainRecipeSearchResultBox(props) {
        return (
            <div className="MainRecipeRankBox" onClick={(e) => {viewRecipeClick(props.recipe_id, props.recipe_nm_ko, props.img)}}>
                <div className="MainRecipeImg"><img src={props.img} alt="Blob URL Image" width={320} height={240}></img></div>
                <div className="MainRecipeName">
                    <span>No. {props.recipe_id}</span>
                    <span>{props.recipe_nm_ko}</span>
                </div>
            </div>
        )
    }

    const [recipeResults, setRecipeResults] = useState([{
        recipe_id : '',
        recipe_nm_ko : '',
        img : '',

    }])

    const getIngredientItemList = 
        // <GetIngredientItem key={v.itemNo} img={v.img} date={v.date} name={v.name} mount={v.mount} />
        inputData.map((vv,index) => {if(index > 0){ return <GetIngredientItem name={vv.ingredientName}  date={vv.shelflife} mount={vv.numb} img={vv.img}></GetIngredientItem>}})

    const expiryItemList = inputData.map((v, index) => {
        const date = new Date();
        const year = date.getUTCFullYear();
        const month = date.getUTCMonth() + 1;
        const day = date.getUTCDate();
    
        let today = String(year) + "-" + String(month) + "-" + String(day);
        let timeLimit = v.shelflife;
    
        const getDateDiff = (d1, d2) => {
            const date1 = new Date(d1);
            const date2 = new Date(d2);
            
            const diffDate = date2.getTime() - date1.getTime();
            
            return Math.ceil(diffDate / (1000 * 60 * 60 * 24)+1); // 밀리세컨 * 초 * 분 * 시 = 일
        }
          
        const getLimitDay = getDateDiff(today, timeLimit);

        if(getLimitDay > 0 && getLimitDay <= 7) {
            isEmptyItem = true;
            return <ExpiryItem img={v.img} date={v.shelflife} name={v.ingredientName} mount={v.numb}></ExpiryItem>
        }
    });
    const selectItemList = txt.map((v, index) => {
        if(index > 0) { 
            isSelectedItem = true;
            return (<ExpiryItem key={v.itemNo} img={v.img} date={v.date} name={v.name} mount={v.mount} />)
        }
        isSelectedItem = false;
    });
    const findRecipeItemList = recipeResults.map((v,index) => {if(index > 0){ return <MainRecipeSearchResultBox recipe_id={v.recipe_id} recipe_nm_ko={v.recipe_nm_ko} img={v.img}/>}})
    

    const [countList, setCountList] = useState([0]);

    const onChangeHandler = (parmas, e) => {
        let optionValue1 = null;
        let optionValue2 = null;

        console.log(parmas);

        /** 
         * params가 1이면 첫번째 셀렉트 값
         * parmas가 2이면 두번째 셀렉트 값
         * */
        if(parmas == "1") {
            optionValue1 = e.target.value;

            setOptionCategory1(optionValue1);
            console.log(optionValue1);
        } else if(parmas == "2") {
            optionValue2 = e.target.value;

            setOptionCategory2(optionValue2);
            console.log(optionValue2);
        } else {
            console.log("Not Value");
        }
    }

    const onClickAddItemHandler = (e) => {
        let countArr = [...countList];
        let counter = countArr.slice(-1)[0];

        if(counter == 0) {
            counter = countArr.splice(-1)[0];
        }
        setAddItem(true);

        if(addItem != false) {
            console.log(addItem);
            Swal.fire({
                icon: 'warning',
                title: '입력중인 칸이 있습니다',
                // timer: 100000,
            })
            return;
        }

        counter += 1;
        countArr.push(counter);

        setCountList(countArr);
    }

    const boxStyle = {
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    };


    return(
        <div className="FridgeContent">
            <div className="FridgeRegiForm">
                <span>나의 냉장고</span>
                <div className="FridgeSearchForm">
                    {/* <select id="searchCategory1" defaultValue={0} onChange={onChangeHandler}> */}
                    <select id="searchCategory1" defaultValue={0} onChange={(e) => {onChangeHandler("1", e)}}>
                        <option value="0">전체</option>
                        <option value="1">냉장</option>
                        <option value="2">냉동</option>
                        <option value="3">실온</option>
                    </select>
                    <select id="searchCategory2" defaultValue={0} onChange={(e) => {onChangeHandler("2", e)}}>
                        <option value="0">등록순</option>
                        <option value="1">남은 기한순</option>
                    </select>
                    <div className="SearchBtn" onClick={(e) => {onClickHandler(optionCategory1, optionCategory2, e)}}>
                        <span>조회</span>
                    </div>
                </div>
                <div className="FridgeRegiResultBox">
                    {getIngredientItemList}
                    <IngredientItem countList={countList} setCountList={setCountList} setAddItem={setAddItem}/>
                    <div className="IngredientAddBtnForm">
                        <div className="btnForm">
                            <div className="editBtn">
                                <i className="fas fa-pen"></i>
                            </div>
                            <div className="deleteBtn">
                                <i className="fas fa-times"></i>
                            </div>
                        </div>
                        <div className="IngredientAddBtn" onClick={(e) => {onClickAddItemHandler(e)}}>
                            <div id="circle">
                                <i className="fas fa-plus"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="pageBtn">
                <div id="circle"></div>
            </div>
            <div className="ExpiryDateForm">
                <span>유통기한 임박 식재료</span>
                {isEmptyItem === false ? 
                    <div className="ExpiryDateBox" style={boxStyle}><span style={{fontSize: "16px"}}>유통기한 임박 식재료가 없습니다</span></div> : <div className="ExpiryDateBox">{expiryItemList}</div>}
            </div>
            <div className="pageBtn">
                <div id="circle"></div>
            </div>
            <div className="SelectedIngredientsForm">
                <span>선택된 식재료</span>
                {console.log(isSelectedItem)}
                {isSelectedItem === false ? <div className="SelectedIngredientsBox" style={boxStyle}><span style={{fontSize: "16px"}}>선택된 식재료가 없습니다</span></div> : <div className="SelectedIngredientsBox">{selectItemList}</div>}
                {/* <div className="SelectedIngredientsBox">
                    {selectItemList}
                </div> */}
                {/* <div className="RecipeSearchResultForm">
                    {findRecipeItemList}
                </div> */}
            </div>
            {isSelectedItem === false ? <></> : 
            <div className="btnForm">
                <div className="btn" onClick={(e) => {
                    // alert(txtData)
                    axios
                        .post("/myfridgeSearch", {
                            txtData0: txtData,
                        })
                        .then((res) => {
                            const _inputData = res.data.map((rowData) => ({
                                recipe_id: rowData.recipe_id,
                                recipe_nm_ko: rowData.recipe_nm_ko,
                                img: rowData.img,
                            })
                        )
                        console.log("_inputData : " + JSON.stringify(_inputData))

                        // if(_inputData.length==0){sethtml("검색결과가 없습니다.")}
                        // else{sethtml("")}
                        setRecipeResults(recipeResults.concat(_inputData))
                    })
                    console.log(recipeResults)
                    setIsClickCheck(true);
                    // recipeResults.splice(1)
                }}>
                    <span>조회</span>
                </div>
            </div>}
            <div className="pageBtn" style={{padding: "40px 0"}}>
                <div id="circle"></div>
            </div>
            {isClickCheck === false ? <div className="RecipeResultForm" style={{display: "none"}}></div> : 
                <div className="RecipeResultForm">
                    <span>선택된 식재료로 만들 수 있는 레시피</span>
                    <div className="RecipeResultBox">
                        {findRecipeItemList}
                    </div>
                    <div className="pageBtn" style={{paddingBottom: "40px"}}>
                        <div id="circle"></div>
                    </div>
                </div>
            }
        </div>
    )
}

function IngredientItem(props) {
    // const [itemData, setItemData] = useState([
    //     {itemCode: "", shelfLife: "", ingredientName: "", num: ""}
    // ])

    const [storeInfoCheck, setStoreInfoCheck] = useState("Not Checked");
    const [date, setDate] = useState(new Date());
    const mountRef = useRef("");
    const ingredientNameRef = useRef("");

    /** 수량 정규식 체크 및 maxlength 설정 */
    const onChangeMountHandler = (e) => {
        const regex = /^[0-9]+$/;
        
        if(!regex.test(e.target.value)) {
            e.target.value.replace('');
        }

        if(e.target.value.length > 2) {
            e.target.value = e.target.value.slice(0, 2);
        }
    }


    const onChangeHandler = (checkThis) => {
        const checkBoxs = document.getElementsByName("checkBox");

        for(let i=0; i<checkBoxs.length; i++) {
            if(checkBoxs[i] !== checkThis) {
                checkBoxs[i].checked = false;
            }
        }
        checkThis.checked ? setStoreInfoCheck(checkThis.value) : setStoreInfoCheck("Not Checked");
    }

    /** 보완 필요 */
    const onClickItemDeleteHandler = (params, key, e) => {
        console.log("Del: " + params);
        console.log("item No: " + key);

        console.log(props.countList);
        console.log(props.setCountList);

        let countArr = [...props.countList];
        let counter = countArr.splice(-1)[key];

        console.log(countArr);
        console.log("cnt: " + counter);

        props.setCountList(countArr);
        props.setAddItem(false);
    }

    /** 보완 필요 */
    const onClickItemAddHandler = (params, key, e) => {
        console.log("Add: " + params);
        console.log("item No: " + key);

        let year = date.getFullYear().toString();
        let month = (date.getMonth() + 1).toString();
        let day = date.getDate().toString();
        let shelfLife = '';
        let ingredientName = document.getElementById('ingredientName').value;
        let num = document.getElementById('num').value;
        let uid = sessionStorage.getItem("id");
        let storeInfo = storeInfoCheck;
        let img = ""

        if(month.length == 1) {
            month = "0" + month;
        }

        if(day.length == 1) {
            day = "0" + day;
        }
        shelfLife = year + month + day;

        if (storeInfo == 1){
            img = "https://cdn-icons-png.flaticon.com/512/3389/3389117.png"
        }else if(storeInfo == 2){
            img = "https://cdn-icons-png.flaticon.com/512/3387/3387466.png"
        }else if(storeInfo == 3){
            img = "https://cdn-icons-png.flaticon.com/512/1619/1619132.png"
        }
        
        console.log(mountRef)

        if(num > 99) {
            Swal.fire({
                icon: 'warning',
                title: '최대 수량은 99개를 넘을 수 없습니다',
                // timer: 100000,
            })
            mountRef.current.value = "99";
            mountRef.current.focus();
            return;
        }

         if(ingredientName == "") {
            Swal.fire({
                icon: 'warning',
                title: '식재료 이름을 입력해주세요',
                // timer: 100000,
            })
            ingredientNameRef.current.focus();
            return; 
        }  else if(num == "") {
            Swal.fire({
                icon: 'warning',
                title: '수량을 입력해주세요',
                // timer: 100000,
            })
            mountRef.current.focus();
            return;
        } else if(storeInfoCheck == "Not Checked") {
            Swal.fire({
                icon: 'warning',
                title: '보관 방법을 선택해주세요',
                // timer: 100000,
            })
            return;
        } else {
            console.log(props.countList);
            console.log(props.setCountList);

            let countArr = [...props.countList];
            let counter = countArr.splice(-1)[key];

            console.log(countArr);
            console.log("cnt: " + counter);

            props.setCountList(countArr);

            //DB에 저장할 값
            // alert("Item Code: " + key + "\n유통기한: " + shelfLife + "\n식재료: " + ingredientName + "\n수량: " + num + "\n보관방법: " + storeInfoCheck);

            axios
            .post("http://localhost:8080/insertIngredient", {
                email: uid,
                storeinfo: storeInfo,
                ingredientName: ingredientName,
                numb: num,
                shelflife: shelfLife,
                img: img,

            }).then((res) => {
                if (res.data == 1) {
                    // alert("등록성공");
                    Swal.fire({
                        icon: 'success',
                        title: '등록성공',
                        // timer: 100000,
                    }).then((q) => {
                      if(q.isConfirmed){
                        document.location.href = "/myfridge";
                      }
                    }
                    );
                }
            }).catch(function (error) {
                console.log(error)
            })
        }
    }

    const handleCalendarClose = () => console.log("Calendar closed");
    const handleCalendarOpen = () => console.log("Calendar opened");

    return (
        <>
            {/* {console.log(props.countList)}
            {console.log("len: " + props.countList.length)} */}
            {props.countList != 0 && props.countList.map((item, i) => (
                <div className="ingredientItemForm" key={i}>
                    <div className="btnForm">
                        <div className="okBtn" onClick={(e) => {onClickItemAddHandler("1", i, e)}}>
                            <i className="fas fa-check"></i>
                        </div>
                        <div className="deleteBtn" onClick={(e) => {onClickItemDeleteHandler("1", i, e)}}>
                            <i className="fas fa-times"></i>
                        </div>
                    </div>
                    <div className="ingredientItem">
                        <div id="img">
                            <span>이미지</span>
                        </div>
                        <div className="ingredientInfoForm">
                            <div className="ingredientInputForm">
                                <div id="shelfLifeForm">
                                    <span>유통기한</span>
                                    {/* <input type="text" id="shelfLife" placeholder="yyyymmdd"></input> */}
                                    <DatePicker selected={date} onChange={(date) => setDate(date)} onCalendarClose={handleCalendarClose} onCalendarOpen={handleCalendarOpen} dateFormat="yyyyMMdd" />
                                </div>
                                <div id="ingredientNameForm">
                                    <span>재료명</span>
                                    <input type="text" id="ingredientName" placeholder="식재료명" ref={ingredientNameRef}></input>
                                </div>
                                <div id="storeInfo">
                                    <label><input type="checkbox" name="checkBox" value="1" onChange={(e) => onChangeHandler(e.target)}></input>{"냉장"}</label>
                                    <label><input type="checkbox" name="checkBox" value="2" onChange={(e) => onChangeHandler(e.target)}></input>{"냉동"}</label>
                                    <label><input type="checkbox" name="checkBox" value="3" onChange={(e) => onChangeHandler(e.target)}></input>{"실온"}</label>
                                </div>
                                {/* <div id="fileUpload">
                                    <input type="file" id="fileUpload" />
                                </div> */}
                                {/* <div id="ingredientBtn">
                                    <div id="delBtn" onClick={(e) => {onClickItemDeleteHandler("1", i, e)}}>삭제</div>
                                    <div id="addBtn" onClick={(e) => {onClickItemAddHandler("0", i, e)}}>추가</div>
                                </div> */}
                            </div>
                            <div className="ingredientCount">
                                <span>수량</span>
                                <input type="number" id="num" min={0} max={99} ref={mountRef} onChange={onChangeMountHandler}></input>
                            </div>
                        </div>
                    </div>
                </div>
            ))}
        </>
    )
}

export default MyFridge;