import Header from "./Header";
import Footer from "./Footer";
import "./css/reset.css"
import "./css/detailDietView.css"
import axios from 'axios'
import React, { useEffect, useState } from "react";

function DetailDietView() {
  const params = new URLSearchParams(window.location.search);

  let d_no = params.get('d_no');
  let diet_name = params.get('diet_name');

  useEffect(() => {
    console.log('식단 번호: ' + d_no + ' 식단 이름: ' + diet_name);
  });
  return (
    <div className="detailDiet">
      <Header />
      <DetailView dietNo={d_no} dietName={diet_name}></DetailView>
      <Footer />
    </div>
  );
}


function DetailView(props) {
  const params = new URLSearchParams(window.location.search);

  let d_no = params.get("d_no");
  let diet_name = params.get("diet_name");

  const [inputData, setInputData] = useState([{
    diet_name: '',
    diet_ex: '',
    keyword: '',
    img:'',
    d_no:'',


  }])

  // 첫 번째 렌더링을 마친 후 실행
  useEffect((es) => {
    const fetchData = async () => {
      try {
        const res = await axios.get('/detail_diet?d_no=' + d_no)
        const _inputData = await res.data.map((rowData) => (
          // setLastIdx(lastIdx + 1),
          {
            diet_name: rowData.diet_name,
            diet_ex: rowData.diet_ex,
            keyword: rowData.keyword,
            d_no:rowData.d_no,
          })
        )
        setInputData(inputData.concat(_inputData))
      } catch (e) {
        console.log(e)
      }
    }
    fetchData();
  }, [])

  console.log(inputData)

  let time = ['아침 메뉴', '점심 메뉴', '저녁 메뉴'];
  let recipe_id = [inputData[2]?.d_no, inputData[3]?.d_no, inputData[4]?.d_no];
  let recipeName = [inputData[2]?.diet_name, inputData[3]?.diet_name, inputData[4]?.diet_name];
  let recipeComment = [inputData[2]?.diet_ex, inputData[3]?.diet_ex, inputData[4]?.diet_ex]
  let dietDes = inputData[1]?.diet_ex;
  let img = [inputData[2]?.keyword, inputData[3]?.keyword, inputData[4]?.keyword]
  let keyword = inputData[1]?.keyword
  return (
    <div className="DetailDietViewBox">
      <div className="DetailDietView">
        <RecipeViewBox time={time[0]} recipe_id={recipe_id[0]} recipeName={recipeName[0]} recipeComment={recipeComment[0]} img={img[0]}></RecipeViewBox>
        <RecipeViewBox time={time[1]} recipe_id={recipe_id[1]} recipeName={recipeName[1]} recipeComment={recipeComment[1]} img={img[1]}></RecipeViewBox>
        <RecipeViewBox time={time[2]} recipe_id={recipe_id[2]} recipeName={recipeName[2]} recipeComment={recipeComment[2]} img={img[2]}></RecipeViewBox>
      </div>
      <div className="DetailDietInfoBox">
        <div className="DetailDeitInfoView">
          <span id="dietInfo">[식단 정보]</span>
          <div className="DetailDietName">
            <span>식단 이름</span>  
            <span>{props.dietName}</span>
          </div>
          <div className="DetailDietDes">
            <span>식단 설명</span>
            <span>{dietDes}</span>
          </div>
          <div className="DetailDietKeyword">
            <span id="dietKeyword">설정된 검색 키워드</span>
            <div className="DetailDietKeywordList">
              <span>{keyword}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

function RecipeViewBox(props) {
  return (
    <div className="DetailRecipeBox">
      <div className="DetailRecipeInfo">
        <span id="timeTitle">{props.time}</span>
        <span>[레시피 이름]</span>
        <span id="detailRecipeName">{props.recipeName}</span>
        <span>[레시피 설명]</span>
        <span id="detailRecipeDes">{props.recipeComment}</span>
      </div>
      <div className="DetailRecipe">
        <div className="DetailRecipeImg" onClick={(e) => {viewRecipeClick(props.recipe_id, props.recipeName)}}><img id="img" src={props.img} alt="Blob URL Image" width={320} height={320}/></div>
      </div>
    </div>
  )
}

const viewRecipeClick = (recipe_id, recipe_nm_ko, e) => {
  console.log(recipe_id + ", " + recipe_nm_ko);

  if (recipe_id != null && recipe_nm_ko != null) {
      window.location.href = "/detail_recipe?recipe_id=" + recipe_id + "&recipe_nm_ko=" + recipe_nm_ko;
  }
}

export default DetailDietView;